<template>
    <div>
        <div class="container">
          <div class="loginBox">
            <div class="loginForm">
                <div class="logo">
                  治疗管理端
<!--                  <img :src="logoUrl" alt="logo">-->
                </div>
                <el-form :model="loginParams" :rules="rules" ref="loginForm">
                  <el-form-item prop="adminName">
                    <el-input class="userid" type="text" placeholder="请输入用户名" v-model="loginParams.adminName"></el-input>
                  </el-form-item>
                  <el-form-item prop="adminPwd">
                    <el-input class="userpw" type="password" placeholder="请输入登录密码" v-model="loginParams.adminPwd"></el-input>
                  </el-form-item>
                  <el-button @click="login()" class="loginBtn">马上登录</el-button>
                </el-form>
            </div>
            <div class="loginImg">
                <img :src="loginImg" alt="login image">
            </div>
        </div>
        </div>
<!--        <div class="footer">
          <div>Copyright 2017 杭州恒生数据安全技术有限公司.All Rights Reserved</div>
        </div>-->
    </div>
</template>


<script>
import AppServerInf from "../common/serverinf"
import router from "@/router";

export default {
  data() {
    return {
      logoUrl: require("../assets/img/logoW.svg"),
      loginImg :require("../assets/img/loginImg.svg"),

      rules:{
        adminName: [{ required: true, message: '请输入用户名', trigger: 'blur'}],
        adminPwd: [{ required: true, message: '请输入密码', trigger: 'blur'}]
      },

      loginParams: {
        adminName: "",
        adminPwd: "",
      }
    };
  },
  methods: {
    login () {
      this.$refs['loginForm'].validate(valid=>{
        if (!valid) return false
        //
        // this.$http.request({
        //   head: AppServerInf.adminLogin,
        //   data: this.loginParams,
        //   _200:res=>{
        //     this.$http.init({token: res})
        //     this.$router.push("/goods")
        //   },
        //   _1001:res=> {
        //     this.$modal.alert('用户名密码错误')
        //   }
        // })
        if (this.loginParams.adminName !== 'admin' && this.loginParams.adminPwd !== '123456') {
          this.$modal.alert('用户名密码错误')
          return false
        }
        sessionStorage.setItem("token", '1')
        this.$router.push("/case")
      })
    },
  },
};
</script>


<style scope>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 100px);
  min-height: 387px;
}
.loginBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  box-shadow: 0 2px 10px #f2f2f2;
}

.logo {
  padding: 20px 0;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
}

.loginForm {
  width: 400px;
  padding: 40px;
  background: #1b6fcb;
}
.loginBox .el-input {
  /*margin: 10px 0;*/
}
.loginBox .el-form-item {
  padding: 10px 0;
  margin: 0;
}
.loginBox .el-form-item .el-form-item__error{
  color: #ffffff;
}
.loginBtn {
  width: 100%;
  margin: 20px 0;
}

.loginImg {
  display: flex;
  justify-content: center;
  width: 400px;
  height: auto;
  padding: 10px 10px 0 10px;
  text-align: center;
}
.loginImg img {
  width: 320px;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
  font-size: 12px;
  color: #ccc;
  text-align: center;
}
</style>



